* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.container {
  color: #212121;
  height: 100vh;
  background: -webkit-linear-gradient(45deg, #d2bd92, #ddcaa0, #f8f3e0);
  margin: 0;
  padding: 30px;
  box-shadow: inset 0 5px 10px 10px #223c5033;
}

.container--color-switcher {
  height: 100vh;
  margin: 0;
  padding: 30px;
  box-shadow: inset 0 5px 10px 10px #223c5033;
}

.container--main {
  justify-content: center;
  align-items: center;
  display: flex;
}

.container--main ul {
  flex-direction: column;
  align-items: center;
  margin: 0;
  display: flex;
}

.container--main a {
  padding: 20px 10px;
}

.container--main li {
  margin: 40px 0 40px -38px;
}

img {
  max-width: 100%;
  display: block;
}

a {
  height: 100px;
  color: #bb056c;
  background: -webkit-linear-gradient(45deg, #d2bd92, #ddcaa0, #f8f3e0);
  border: 4px solid #bb056c;
  border-radius: 10px;
  margin: 5px 0;
  padding: 2px 4px;
  font-size: 24px;
  font-weight: 700;
  text-decoration: none;
  box-shadow: 5px 5px 13px #223c5099;
}

.go-back {
  color: #f8f3e0;
  background: -webkit-linear-gradient(90deg, #bb056c, #e72d44);
  border: 2px solid #000;
  padding: 4px;
}

.go-back:hover {
  border: 2px solid #e72d44;
}

ul {
  list-style: none;
}

li {
  margin: 20px;
}

label {
  color: #bb056c;
  font-size: 18px;
  font-weight: 700;
}

.back {
  margin: 0;
}

.calendar {
  flex-direction: column-reverse;
  align-items: center;
  display: flex;
}

input {
  max-width: 300px;
  text-align: center;
  color: #fff;
  mix-blend-mode: multiply;
  background: -webkit-linear-gradient(90deg, #c3284d, #f76463);
  border: 0;
  border-radius: 5px;
  margin-bottom: 20px;
  font-size: 36px;
  display: table;
  box-shadow: 5px 5px 13px #223c5099;
}

button[data-start], button[data-stop] {
  cursor: pointer;
  color: #f5f5f5;
  background-color: #c3284d;
  border: 0;
  border-radius: 5px;
  margin: 10px auto 40px;
  padding: 30px 40px;
  font-size: 28px;
  display: table;
}

button[data-start]:hover, button[data-stop]:hover {
  background-color: #ed4155;
}

button[data-start]:disabled, button[data-stop]:disabled {
  background-color: #acacac;
}

.timer {
  width: 700px;
  border: 10px solid red;
  border-image: -webkit-linear-gradient(90deg, #bb056c, #e72d44) 1;
  justify-content: center;
  margin: 100px auto 0;
  font-size: 72px;
  display: flex;
  box-shadow: 5px 5px 13px #223c5099;
}

.field {
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  display: flex;
  position: relative;
  box-shadow: 5px 5px 13px #223c5099;
}

.label {
  font-size: 18px;
  font-weight: 700;
}

.value {
  min-width: 100px;
  text-align: center;
  color: #fff;
  mix-blend-mode: multiply;
  background: -webkit-linear-gradient(90deg, #c3284d, #f76463);
  border-radius: 5px;
  padding-bottom: 15px;
  display: table;
}

.field:before {
  content: attr(data-title);
  font-size: 14px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.field:not(:last-child):after {
  content: ":";
  position: absolute;
  right: -17px;
}

.submit-btn {
  width: 300px;
  color: #f5f5f5;
  cursor: pointer;
  background: -webkit-linear-gradient(90deg, #bb056c, #e72d44);
  border: 0;
  border-radius: 4px;
  margin: 10px auto 40px;
  padding: 12px 15px;
  font-size: 18px;
  box-shadow: 5px 5px 13px #223c5099;
}

